.sidebar {
  width: 249px;
  height: 100%;
  border-right: 1px solid #dbdbdb;
}
.sidebarItem {
  margin-left: 21.5px;
}
.sidebarList {
  padding-top: 33px;
  padding-bottom: 33.5px;
}

.row {
  list-style-type: none;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  cursor: pointer;
}
.row:hover {
  background: rgba(80, 48, 229, 0.08);
  border-radius: 6px;
  margin: 5px;
}
.row .icon {
  padding: 12.5px 17px 12.5px 0;
  align-items: left;
  color: #787486;
  width: 20px;
  height: 20px;
}
.row .name {
  display: flex;
  font-size: 15px;
  color: #787486;
}

.sidebarHr {
  margin: 0 5%;
  background-color: 1px solid #787486;
}
.myProject {
  width: 203px;
  height: 16px;
  display: flex;
  justify-content: space-between;
  margin-top: 30.5px;
}
.myProject h4 {
  font-size: 12px;
  color: #787486;
}
.proIcon {
  color: #787486;
  height: 13.33px;
}
.projectList {
  margin-bottom: 55px;
}
.projectList li {
  padding: 10px 0;
  border-radius: 6px;
  color: #787486;
  cursor: pointer;
  display: flex;
  /* justify-content: space-between; */
}
.bullet {
  color: #7ac555;
  padding: 0;
  margin-right: 16px;
}
.pname {
  font-size: 16px;
  margin-right: 40px;
}
.threedot {
  visibility: hidden;
}
#active .threedot {
  visibility: visible;
}

.projectList :hover {
  color: #0d062d;
  font-weight: 600;
  background-color: rgba(80, 48, 229, 0.08);
}

/* feedback  */

.feedback {
  text-align: center;
  width: 206px;
}
.feedbackBorder {
  border-radius: 16px;
  padding: 20px;
  margin-top: 32px;
  background-color: rgba(196, 196, 196, 1);
}
.feedbackBorder h4 {
  margin-top: 15px;
  font-size: 14px;
}
.feedbackBorder p {
  margin-top: 12px;
  margin-bottom: 14px;
  color: rgba(120, 116, 134, 1);
  font-size: 12px;
}
.feedbackBorder .input {
  height: 40px;
  width: 166px;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 4px;
}
.feedbackBorder input {
  border: none;
  font-size: 14px;
  color: black;
  font-weight: 500;
  height: 17px;
  width: 112px;
  margin: 12px 27px;
}
.lightBackground {
  margin: -30px -30px;
  position: absolute;
  z-index: 1;
  background-color: rgba(196, 196, 196, 1);
  height: 66px;
  width: 66px;
  display: inline-block;
  border-radius: 50%;
  /* box-shadow: inset 10px 10px 10px 10 px #fbcb18; */
}

.lightIcon {
  padding: 16px 17px 17px 16px;
  height: 24px;
  width: 24px;
  color: #fbcb18;
  background: radial-gradient(
    circle,
    rgba(251, 203, 24, 0.3) 0%,
    rgba(196, 196, 196, 0) 99%
  );
}

/* sidebar end */
.mainHeader {
  display: flex;
}

.logo {
  /* height: 88px; */
  /* width: 250px; */
  border-right: 1px solid #dbdbdb;
  border-bottom: 1px solid #dbdbdb;
  display: flex;
  align-items: center;
  padding: 16px 14px 16px 17px;
  /* justify-content: space-between;  */
}

.logoImage {
  height: 24px;
  width: 24px;
  margin-right: 9px;
}
.logoTitle {
  font-size: 20px;
  color: #0d062d;
  margin-right: 46px;
}
/* margin-top: 32px;
  padding-right: 30.5px;
  display: inline-block;
  height: 24px;
  width: 205;
  padding-bottom: 32px;*/

.toggle {
  width: 20px;
  height: 20px;
  color: #787486;
}
.hide {
  visibility: hidden;
}

/* header start  */

.header {
  width: 1190px;
  border-bottom: 1px solid #dbdbdb;
  height: 88px;
  display: flex;
  justify-content: space-between;
}
.searchBox {
  width: 417px;
  background-color: rgba(245, 245, 245, 1);
  margin: 22px 0 22px 48.5px;
  display: flex;
  border-radius: 6px;
  /* align-items: center; */
  /* justify-content: center; */
}
.searchBox input {
  outline: none;
  border: none;
  background-color: rgba(245, 245, 245, 1);
  color: rgba(120, 116, 134, 1);
  font-size: 14px;
}
.searchIcon {
  padding: 12px 18.75px 14.75px 18.63px;
  align-items: center;
  height: 17.42px;
  width: 17.42px;
  color: rgba(120, 116, 134, 1);
}

/* header right  */
.headerRight {
  display: flex;
  flex-direction: row;
}
.headerIcons {
  margin: 32px 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 120px;
  height: 24px;
}
.headerIcon {
  height: 20px;
  width: 20px;
  color: rgba(120, 116, 134, 1);
}
.profile {
  margin: 24px 50px 0 0;
  display: flex;
  flex-direction: row;
}
.profileLeft {
  margin-right: 18px;
  display: flex;
  flex-direction: column;
}
.profileRight {
  display: flex;
}

.profilePic {
  height: 38px;
  width: 38px;
  border-radius: 50%;
  overflow: hidden;
  background-color: #0d062d;
}
.userCity {
  font-size: 14px;
  color: #787486;
}
.userName {
  font-size: 16px;
  color: rgba(13, 6, 45, 1);
}
.dropDown {
  margin: 11px 7.5px;
  height: 18px;
  width: 18px;
  color: #292d32;
}
/* Header end  */

/* titleSection start  */
.titleSection {
  float: right;
  width: 1190px;
  height: 209px;
  display: flex;
  justify-content: space-between;
}
.titleLeft {
  padding: 41px 0 42px 48.5px;
}
.titleRight {
  padding: 41px 48px 42px 0;
}

.titleLink {
  display: flex;
}
.titleLink h1 {
  font-size: 46px;
  font-weight: 600;
  margin: 0 21px 40px 0;
}
.titleLink a {
  background-color: #dbdbdb;
  height: 30px;
  width: 30px;
  margin-right: 17px;
  margin-top: 18px;
  text-align: center;
  background-color: rgba(80, 48, 229, 0.2);
  border-radius: 4px;
}
.titleLink a .penicon {
  font-size: 15.51px;
  margin: auto;
  padding-top: 6px;
  color: rgba(80, 48, 229, 1);
}
.dropdown {
  display: flex;
  width: 256px;
  height: 40px;
  display: flex;
  justify-content: space-around;
  position: static;
}
.dropDownItem {
  font-size: 16px;
  color: #787486;
  padding: 10px 16px;
  border-radius: 6px;
  border: 1px solid #787486;
  font-weight: 500;
}
.inviteSection {
  display: flex;
  justify-content: space-between;
}
.inviteSection span {
  font-size: 16px;
  color: rgba(80, 48, 229, 1);
  margin-right: 12px;
  align-items: center;
  display: flex;
}
.friendImage {
  width: 158px;
  height: 38px;
  position: relative;
  left: -3px;
}
.friendImage1 {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
}
.friendImage2 {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 30px;
}
.friendImage3 {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 60px;
}
.friendImage4 {
  position: absolute;
  z-index: 4;
  top: 0;
  left: 90px;
}
.friendImage5 {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 120px;
}
.friendImage span {
  font-size: 15px;
  font-weight: 500;
  color: rgba(210, 91, 104, 1);
  padding: 8px 7px;
}

/* titleSection end  */
.shearSection{
  display: flex;
margin-top: 49px;
}
.share{
  font-size: 16px;
  color: #787486;
  padding: 10px 16px;
  border-radius: 6px;
  border: 1px solid #787486;
  font-weight: 500;
  margin-right: 20px;
}